
import Xheader from '@/components/comother/rheader'
import Gain from '@/components/comother/gain'
import ViewBox from 'vux/src/components/view-box/index.vue'

import axios from 'axios'
export default {
data(){
return {
contents:{rights:'',title:'积分获取明细'},
list:[],
temporarily:false
}
},
components:{
'r-header':Xheader,
'gain':Gain,
ViewBox
},
methods:{
details(){
axios.get('pscoreparty/queryByUserId',{
params:{
userid:this.$route.params.userid,
year:this.$route.params.year
}
}).then(res =>{
console.log(res);
if(res.data.length == 0){
this.temporarily = true;
}
this.list = res.data;
}).catch(err =>{
console.log('fail'+err);
})
}
},
mounted(){
this.details();
}
}



